<template>
  <div class="col-4">
    <div class="panel">
      <div class="panel-heading">
        <div class="panel-title">案件状态分析</div>
      </div>
      <div class="panel-body" v-loading="loading" style="overflow: auto;">
        <div class="chart" id="chart5"></div>
      </div>
    </div>
  </div>
</template>

<script>
import watchDate from '@/mixins/watchDate'
export default {
  props: ['dateType'],
  data () {
    return {
      loading: false
    }
  },
  mixins: [
    watchDate
  ],
  mounted () {
    this.init()
  },
  methods: {
    init () {
      var self = this
      this.loading = true
      this.$http.get({
        api: 'A_S_CASE_STATUS',
        params: {
          dateType: this.dateType
        },
        loading: this.$data,
        callback: (res) => {
          self.loading = false
          self.draw(res)
        }
      })
    },
    draw (a) {
      let d = this.dataFormat(a)
      let option = {
        tooltip: {
          trigger: 'axis',
          axisPointer: { // 坐标轴指示器，坐标轴触发有效
            type: 'shadow' // 默认为直线，可选为：'line' | 'shadow'
          }
        },
        legend: {
          data: d.legend
        },
        grid: {
          left: '3%',
          right: '4%',
          bottom: '3%',
          containLabel: true
        },
        xAxis: {
          type: 'value'
        },
        yAxis: {
          type: 'category',
          data: d.yAxis,
          axisLabel: {
            formatter: function (value) {
              if (value.length > 5) {
                return value.substring(0, 5) + '...'
              }
              return value
            }
          }
        },
        series: d.series
      }
      let myChart = this.$echarts.init(document.getElementById('chart5'), 'macarons')
      myChart.setOption(option)
    },
    dataFormat (a) {
      let obj = {
        series: [],
        yAxis: [],
        legend: []
      }
      obj.legend = a.category
      let yjaData = []
      let blzData = []
      a.series.map(o => {
        obj.yAxis.push(o.name)
        yjaData.push(o.data[0])
        blzData.push(o.data[1])
      })
      obj.series = [{
        name: '办结',
        type: 'bar',
        stack: '总量',
        label: {
          normal: {
            show: true,
            position: 'insideRight'
          }
        },
        data: yjaData
      },
      {
        name: '办理中',
        type: 'bar',
        stack: '总量',
        label: {
          normal: {
            show: true,
            position: 'insideRight'
          }
        },
        data: blzData
      }
      ]
      return obj
    }
  }
}

</script>



